import React, { Component } from 'react'
import posed from 'react-pose'
import { confetti } from '../../confetti'
import { emoji } from '../../emoji'

const SpringAnim = posed.div({
  clicked: {
    y: 5,
    transition: {
      type: 'spring',
      stiffness: 200,
      damping: 2
    }
  },
  resting: {
    y: 0,
    transition: {
      type: 'spring',
      stiffness: 200,
      damping: 2
    }
  }
})

export default class Reward extends Component {
  state = {
    animate: false
  }
  constructor (props) {
    super(props)
    this.setRef = this.setRef.bind(this)
  }

  componentDidUpdate (prevProps) {
    const shouldRewardUser = prevProps.active
    switch (prevProps.type) {
      case 'confetti': {
        if (!shouldRewardUser) return
        this.handleAnimation()
        confetti(this.container, prevProps.config)
        break
      }
      case 'emoji': {
        if (!shouldRewardUser) return
        this.handleAnimation()
        emoji(this.container, prevProps.config)
        break
      }
      default: {
        break
      }
    }
  }

  handleAnimation = () => {
    setTimeout(() => {
      this.setState({ animate: true }, () => {
        setTimeout(() => {
          this.setState({ animate: false })
        }, 100)
      })
    }, 100)
  }

  setRef (ref) {
    this.container = ref
  }

  render () {
    const { config, children } = this.props
    const { animate } = this.state
    const { springAnimation } = config
    return (
      <span>
        <div ref={this.setRef} />
        <SpringAnim pose={springAnimation && (animate ? 'clicked' : 'resting')}>
          {children}
        </SpringAnim>
      </span>
    )
  }
}



